.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.prank{
  background-color: rgb(199, 37, 199);
  height: 100px;
  color: white;
  font-size: 30px;
  font-weight: bold;
}









/* Reset default margin and padding for better consistency */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}

.footer-bottom {

  width: 800px;
  height: 200px;
  background-color: #2858a4;
  color: #fff;
  text-align: center;
  padding: 30px 0;
  border-radius: 20px;
  border: 15px solid rgb(80, 80, 80);
}
.footer-position{
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  
  
}




.footer-bottom p {
  margin-bottom: 10px;
}

.footer-links {
  margin: 10px 0;
}

.footer-links a {
  color: #ffc107;
  text-decoration: none;
  margin: 0 5px;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: #fff;
  text-decoration: underline;
}

.footer-bottom p:last-child {
  margin-top: 20px;
}

/* Responsive adjustments */
@media screen and (max-width: 768px) {
  .footer-links {
      display: block;
      margin: 10px 0;
      height: 200px;
  }
  
 
}
